import { featureFlagsSelectors } from 'store/featureFlags';
import { getCurrency } from 'utils/currency';
import { getQueryForWidget, getQueryForWidgetTabs, ocpCloudDashboardDefaultFilters, ocpCloudDashboardStateKey, ocpCloudDashboardTabFilters, } from './ocpCloudDashboardCommon';
export const selectOcpCloudDashboardState = (state) => state[ocpCloudDashboardStateKey];
export const selectWidgets = (state) => selectOcpCloudDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpCloudDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, ocpCloudDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ocpCloudDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    const props = Object.assign({}, (featureFlagsSelectors.selectIsCurrencyFeatureEnabled(state) && { currency: getCurrency() }));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 }), props),
        current: getQueryForWidget(defaultFilter, props),
        forecast: getQueryForWidget({}, Object.assign({ limit: 31 }, props)),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' }), props),
    };
};
