import { useUnleashClient, useUnleashContext } from '@unleash/proxy-client-react';
import React, { useLayoutEffect, useRef } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { featureFlagsActions } from 'store/featureFlags';
let userId;
const insights = window.insights;
if (insights && insights.chrome && insights.chrome.auth && insights.chrome.auth.getUser) {
    insights.chrome.auth.getUser().then(user => {
        userId = user.identity.account_number;
    });
}
// The FeatureFlags component saves feature flags in store for places where Unleash hooks not available
const FeatureFlagsBase = ({ children = null }) => {
    const updateContext = useUnleashContext();
    const client = useUnleashClient();
    const dispatch = useDispatch();
    const isMounted = useRef(false);
    useLayoutEffect(() => {
        isMounted.current = true;
        return () => {
            isMounted.current = false;
        };
    }, []);
    // Update everytime or flags may be false
    useLayoutEffect(() => {
        if (userId && isMounted.current) {
            updateContext({
                userId,
            });
        }
    });
    useLayoutEffect(() => {
        // Wait for the new flags to pull in from the different context
        const fetchFlags = async () => {
            await updateContext({ userId }).then(() => {
                dispatch(featureFlagsActions.setFeatureFlags({
                    isCurrencyFeatureEnabled: client.isEnabled("cost-management.ui.currency" /* FeatureToggle.currency */),
                    isExcludesFeatureEnabled: client.isEnabled("cost-management.ui.negative-filtering" /* FeatureToggle.excludes */),
                    isExportsFeatureEnabled: client.isEnabled("cost-management.ui.exports" /* FeatureToggle.exports */),
                    isIbmFeatureEnabled: client.isEnabled("cost-management.ui.ibm" /* FeatureToggle.ibm */),
                    isOciFeatureEnabled: client.isEnabled("cost-management.ui.oci" /* FeatureToggle.oci */),
                }));
            });
        };
        if (userId && isMounted.current) {
            fetchFlags();
        }
    });
    return React.createElement(React.Fragment, null, children);
};
const FeatureFlags = withRouter(FeatureFlagsBase);
export default FeatureFlags;
